
.listSize{
  top: 0;
  width: 100%;
  margin-bottom: 4%;
  border: 2px solid black;
  border-radius: 30px;
  padding: 10px;
  text-align: center;
  background: linear-gradient(to right,#faaca8,  #7b4397);
}

.listSize > button >img{
  width: 25%;
  display: inline-block;
  vertical-align: middle;
}
 .listSize>button{
  width: 90%;
  text-align: left;
  padding: 5px 10px 5px 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  background-color: white;
}
.listSize > button > p{
  display: inline-block;

 
}
