.cartviewer{
    width:90%;
    border:2px solid black;
    border-radius: 10px;
    display: inline-block;
    color: black;
    margin: 5%;
    padding: 10px;
}

.cartviewer > div > img{
  width: 12%;
  display: inline-block;
  vertical-align: middle;
  border-radius: 10px;
  text-align: left;
}
.cartviewer >div > p {
    display: inline-block;
    margin-left: 2%;
    vertical-align: middle;
    text-align: left;
}
.cartviewer > div > button{
    position: sticky;
    border: 2px solid transparent ;
    background: transparent;
    text-align: right;
    left: 60%;
}
.cartviewer > div > button:hover{
    background-color: #faaca8;
    border: 2px solid black ;
    border-radius: 5px;
}
.cartviewer > h4{
    text-align: center;
}
.cartviewer hr{
    color: black;
    background-color: black;
}

