  .ProductViewer{
      display: inline-block; 
      margin-bottom: 20px;
  }
  
  
  .ProductViewer > div:nth-child(1) > img{
    width: 60%;
    box-sizing:border-box;
    margin: 0 20% 0 20%;
    padding: 30px;
    display:inline-block;
    text-align: center; 
    border-radius: 50px; 
  }
  .ProductViewer > div:nth-child(1){
      display: inline-block;
      width: 50%;
      vertical-align: middle;
    
  }
  .ProductViewer > div:nth-child(1) >button >img{
      width: 30%;
  }
  .ProductViewer > div:nth-child(1) >button{
      border: 2px solid transparent;
      width:17%;
      margin: 0 16% 10% 16%;
      background-color: transparent;
      border-radius: 20px;
  }
  .ProductViewer > div:nth-child(1) >button:hover{
      border: 2px solid black;
    background-color: #7b4397;

      
  }
  
  .ProductViewer > div:nth-child(2){
    display: inline-block;
    vertical-align: middle;
    width: 40%;}

  .ProductViewer > div:nth-child(2) > button{
      margin-top: 20px;
      border-radius: 20px;
      padding: 10px;
      color: white;
      background-color: transparent;
  }
  .ProductViewer > div:nth-child(2) > button:hover{
      width:109px;
      transform: translateX(-10px);
      color:black;
      background-color: #faaca8;

  }