*{
  font-family: "Courier New",Courier,monospace;
  margin: 0px;
  padding: 0;

}


.productSize{
  border: 2px solid black;
  border-radius: 30px;
  display: inline-block;
  width: 66%;
  margin: 2%;
  
  vertical-align: top;
  background: linear-gradient(to right,#faaca8, #7b4397);
}
.sideBar{

  left:70%;
  display: inline-block;
  width: 25%;
  margin: 2%;
  height: 100vh;
  margin-left: 0;
  height: 100%;
}
