.topbar{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    border: 2px solid black;
    
    background-color:#faaca8;
    
    
  }
  .topbar > img{
    width: 10%;
    height: 80%;
    border: 2px solid black;
    border-radius: 20px;
    margin: 5px 0; 
    display: inline-block;
    vertical-align: middle;
    
  }
  .topbar ul {
    float: right;
    display: inline-flex;
    align-items: right;
    
  }
  .topbar ul li{
    display: inline-block;
    text-decoration: none;
    margin-right: 20px;
  
  }
  .topbar > button{
    display: inline-block;
   margin-left: 82%;
    padding: 8px 5px;
    border-radius: 20px;
    border: 2px solid black;
    
   
  }